import { defineComponent, onMounted } from "vue";
import AOS from "aos";
import { defaultImgUrl } from "@/const/appConsts";
export default defineComponent({
  name: "TripCard",
  emits: ["card_clicked"],
  props: {
    card_data: {
      type: Object
    }
  },

  setup(props, {
    emit
  }) {
    onMounted(() => {
      AOS.init();
    });

    const cardClicked = () => {
      emit("card_clicked", props.card_data);
    };

    return {
      cardClicked,
      defaultImgUrl
    };
  }

});