import { defineComponent, onMounted, toRefs } from "vue";
import AOS from "aos";
import NewsContent from "@/components/HomePage/NewsContent.vue";
import TripCard from "@/components/TripCard.vue";
import { reactive } from "@vue/reactivity";
import getRandomInt from "@/utils/getRandom";
import router from "@/router";
import RouterNames from "@/router/name";
export default defineComponent({
  name: "HomeNews",
  components: {
    NewsContent,
    TripCard
  },
  props: {},

  setup(props) {
    const state = reactive({
      first_card_span: 12,
      sec_card_span: 12,
      svg_width: 300,
      card_one: {},
      card_two: {},
      card_three: {},
      card_fore: {}
    });

    const setSpan = () => {
      if (window.innerWidth > 1024) {
        state.first_card_span = 12;
        state.sec_card_span = 12;
      } else {
        state.first_card_span = 16;
        state.sec_card_span = 0;
      }
    };

    window.addEventListener("resize", () => {
      console.log(window.innerWidth);
      state.svg_width = window.innerWidth / 2;
      setSpan();
    });

    const getPopularData = async () => {
      const activity = await require("@/assets/jsonData/activity.json");
      activity.map(_ => {
        state.card_one = activity[getRandomInt(activity.length + 1)];
        state.card_two = activity[getRandomInt(activity.length + 1)];
        state.card_three = activity[getRandomInt(activity.length + 1)];
        state.card_fore = activity[getRandomInt(activity.length + 1)];
      });
    };

    onMounted(() => {
      AOS.init();
      setSpan();
      getPopularData();
    });

    const clickedFun = data => {
      console.log("data", data);
      router.push({
        name: RouterNames.tripPage,
        params: {
          id: data.actId
        }
      });
    };

    const goMoreUrl = () => {
      // ElMessage("Coming soon")
      router.push({
        name: RouterNames.tripList
      });
    };

    return { ...toRefs(state),
      clickedFun,
      goMoreUrl
    };
  }

});