import { defineComponent, onMounted, toRefs } from "vue";
import AOS from "aos";
import TripCard from "@/components/TripCard.vue";
import { reactive } from "@vue/reactivity";
import getRandomInt from "@/utils/getRandom";
import router from "@/router";
import RouterNames from "@/router/name";
export default defineComponent({
  name: "HomeGroup",
  components: {
    TripCard
  },
  props: {},

  setup(props) {
    const state = reactive({
      first_card_span: 8,
      sec_card_span: 8,
      third_card_span: 8,
      card_one: {},
      card_two: {},
      card_three: {}
    });

    const setSpan = () => {
      if (window.innerWidth > 768) {
        state.first_card_span = 8;
        state.sec_card_span = 8;
        state.third_card_span = 8;
      } else {
        state.first_card_span = 10;
        state.sec_card_span = 10;
        state.third_card_span = 0;

        if (window.innerWidth <= 550) {
          state.first_card_span = 18;
          state.sec_card_span = 0;
          state.third_card_span = 0;
        }
      }
    };

    window.addEventListener("resize", () => {
      console.log(window.innerWidth);
      setSpan();
    });

    const getPopularData = async () => {
      const activity = await require("@/assets/jsonData/activity.json"); // console.log("activity", activity)

      activity.map(_ => {
        state.card_one = activity[getRandomInt(activity.length + 1)];
        state.card_two = activity[getRandomInt(activity.length + 1)];
        state.card_three = activity[getRandomInt(activity.length + 1)];
      });
    };

    onMounted(() => {
      AOS.init();
      setSpan();
      getPopularData();
    });

    const clickedFun = data => {
      console.log("data", data);
      router.push({
        name: RouterNames.tripPage,
        params: {
          id: data.actId
        }
      });
    };

    const goMoreUrl = () => {
      // ElMessage("Coming soon")
      router.push({
        name: RouterNames.tripList
      });
    };

    return { ...toRefs(state),
      clickedFun,
      goMoreUrl
    };
  }

});