import { defineComponent, onMounted, toRefs } from "vue";
import { reactive } from "@vue/reactivity";
import SvgIcon from "@/components/SvgIcon.vue";
import router from "@/router";
import RouterNames from "@/router/name";
export default defineComponent({
  name: "SearchInput",
  components: {
    SvgIcon
  },
  props: {},

  setup(props) {
    const state = reactive({
      city_name: "",
      area_name: "",
      cityArr: [],
      activity: [],
      areaArr: []
    });

    const getCityOption = async () => {
      state.activity = await require("@/assets/jsonData/activity.json");
      let cityList = [];
      state.activity.map(_ => {
        cityList.push(_.cityName.split(" ")[0]);
      });
      const cityNameList = cityList.filter((city, idx) => {
        return cityList.indexOf(city) == idx;
      });
      cityNameList.map(_ => {
        state.cityArr.push({
          label: _,
          value: _
        });
      });
    };

    onMounted(() => {
      getCityOption();
    });

    const citySelected = () => {
      state.areaArr = [];

      if (state.city_name != "") {
        const areaArr = state.activity.filter(_ => {
          return _.cityName.includes(state.city_name) == true;
        });
        const areaList = [];
        areaArr.map(_ => {
          if (_.cityName.split(" ")[2] != undefined) {
            areaList.push(_.cityName.split(" ")[2]);
          }
        });
        const areaNameList = areaList.filter((city, idx) => {
          return areaList.indexOf(city) == idx;
        });
        areaNameList.map(_ => {
          state.areaArr.push({
            label: _,
            value: _
          });
        });
      }
    };

    const searchSubmit = () => {
      // ElMessage("Coming soon")
      const selectedTrip = state.activity.filter(_ => {
        if (state.area_name != "") {
          return _.cityName.includes(state.area_name) == true;
        } else {
          return _.cityName.includes(state.city_name) == true;
        }
      });
      console.log("selectedTrip", selectedTrip);

      if (selectedTrip.length > 1) {
        if (selectedTrip.length == state.activity.length) {
          router.push({
            name: RouterNames.tripList
          });
        } else {
          let keyword;

          if (state.area_name == "") {
            keyword = selectedTrip[0].cityName.split(" ")[0];
            router.push({
              name: RouterNames.tripList,
              params: {
                keyword: keyword
              }
            });
          } else {
            keyword = selectedTrip[0].cityName.split(" ")[0] + "," + selectedTrip[0].cityName.split(" ")[2];
            router.push({
              name: RouterNames.tripList,
              params: {
                keyword: keyword
              }
            });
          }
        }
      } else {
        router.push({
          name: RouterNames.tripPage,
          params: {
            id: selectedTrip[0].actId
          }
        });
      }
    };

    return { ...toRefs(state),
      citySelected,
      searchSubmit
    };
  }

});